import type {
  FileUploadData,
  FileUploadHeader,
  HandleFileResponseFunction,
  HandleFileUrlsFunction,
  FileChunkUploadConfig,
  FileChunkUploadFunction,
  FileChunkMergeFunction,
} from '@liv-web/utils';
import type { InjectionKey } from 'vue';

/**
 * 文件上传地址key
 */
export const FILE_UPLOAD_URL_KEY = Symbol('FILE_UPLOAD_URL_KEY') as InjectionKey<string>;

/**
 * 文件上传header key
 */
export const FILE_UPLOAD_HEADER_KEY = Symbol(
  'FILE_UPLOAD_HEADER_KEY',
) as InjectionKey<FileUploadHeader>;

/**
 * 文件上传data key
 */
export const FILE_UPLOAD_DATA_KEY = Symbol('FILE_UPLOAD_DATA_KEY') as InjectionKey<FileUploadData>;

/**
 * 将数据库存储的文件地址转换为可以访问的文件地址数组key
 */
export const HANDLE_FILE_URLS_KEY = Symbol(
  'HANDLE_FILE_URLS_KEY',
) as InjectionKey<HandleFileUrlsFunction>;

/**
 * 将数据库存储的图片地址转换为可以访问的缩略图地址数组key
 */
export const HANDLE_THUMB_URLS_KEY = Symbol(
  'HANDLE_THUMB_URLS_KEY',
) as InjectionKey<HandleFileUrlsFunction>;

/**
 * 处理文件上传请求返回的response key
 */
export const HANDLE_FILE_RESPONSE_KEY = Symbol(
  'HANDLE_FILE_RESPONSE_KEY',
) as InjectionKey<HandleFileResponseFunction>;

/**
 * 切片上传配置 key
 */
export const FILE_CHUNK_UPLOAD_CONNFIG_KRY = Symbol(
  'FILE_CHUNK_UPLOAD_CONNFIG_KRY',
) as InjectionKey<FileChunkUploadConfig>;

/**
 * 切片上传请求 key
 */
export const FILE_CHUNK_UPLOAD_FUNCTION_KEY = Symbol(
  'FILE_CHUNK_UPLOAD_FUNCTION_KEY',
) as InjectionKey<FileChunkUploadFunction>;

/**
 * 切片合并请求 key
 */
export const FILE_CHUNK_MERGE_FUNCTION_KEY = Symbol(
  'FILE_CHUNK_MERGE_FUNCTION_KEY',
) as InjectionKey<FileChunkMergeFunction>;
